import {
  boothInternshipLine1,
  boothInternshipLine2,
  boothScholarshipLine1,
  boothScholarshipLine2,
} from '../../../data/companies/Booth1x1';
import CompanyThumbCard from './CompanyThumbCard';

export default function ScholarInter() {
  return (
    <div className="flex h-[11vh] w-full justify-between gap-[4.2vw]">
      <div className="h-[8vh] flex-1 bg-cyan-600/0">
        <div title="divider" className="h-[1.6vh] w-full bg-red-500/0" />
        <div className="flex justify-end gap-[0.85vw]">
          {boothScholarshipLine1.map((company, i) => (
            <CompanyThumbCard
              notShow={company.isNotShow}
              hSize="4vh"
              className="bg-purple-200/0"
              wSize="2.6vw"
              companyDetail={company}
              key={Math.random()}
            />
          ))}
        </div>
        <div title="divider" className="h-[1.2vh] w-full bg-red-500/0" />
        <div className="flex justify-end gap-[1vw]">
          {boothScholarshipLine2.map((company, i) => (
            <CompanyThumbCard
              className="bg-purple-200/0"
              notShow={company.isNotShow}
              hSize="4vh"
              wSize="2.7vw"
              companyDetail={company}
              key={Math.random()}
            />
          ))}
        </div>
      </div>

      <div className="-ml-2.5 h-[11vh] flex-1 bg-cyan-600/0">
        <div title="divider" className="h-[1.6vh] w-full bg-red-500/0" />
        <div className="flex justify-start gap-[0.85vw]">
          {boothInternshipLine1.map((company, i) => (
            <CompanyThumbCard
              notShow={company.isNotShow}
              hSize="4vh"
              className="bg-purple-200/0"
              wSize="2.6vw"
              companyDetail={company}
              key={Math.random()}
            />
          ))}
        </div>
        <div title="divider" className="h-[1.2vh] w-full bg-red-500/0" />
        <div className="flex justify-start gap-[1vw]">
          {boothInternshipLine2.map((company, i) => (
            <CompanyThumbCard
              notShow={company.isNotShow}
              className="bg-purple-200/0"
              hSize="4vh"
              wSize="2.6vw"
              companyDetail={company}
              key={Math.random()}
            />
          ))}
        </div>
      </div>
    </div>
  );
}
